body {
    margin: 0;
    padding: 0;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.adminlogin-wrapper{
    height: 100vh;
    align-items: center;
}

.logo {
    position: absolute;
    bottom: 10px; /* Adjust as needed */
    left: 10px; /* Adjust as needed */
    width: 50px; /* Adjust as needed */
    height: auto; /* Allow height to adjust automatically */
}



.adminlogin-container {
    max-width: 600px; /* Increased max-width */
    width: 100vw; /* Responsive width */
    padding: 30px; /* Increased padding */
    margin: 20px auto; /* Center the container */
    background-color: #efd3ff; /* Background color */
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 0 20px rgba(0, 1, 0, 1); /* Shadow effect */
    text-align: center; /* Align text to center */
    height: 95vh;
    box-sizing: border-box; /* Include padding and border in the total width */
    overflow: auto;
}

.adminlogin-container h2{
    margin: 10px;
}

.adminlogin-container::-webkit-scrollbar{
    display: none;
}

.loginbutton{
    height: 10vh;
    width: auto;
    cursor: pointer;
    transition: background-color 0.3s; 
    border-radius: 15px;

}

.loginbutton:hover {
    padding: 10px;
    border-radius: 15px;
}


.form-group {
    margin-bottom: 20px; /* Increased margin-bottom */
    display: flex;
    flex-direction: column;
    align-items: center;
}


label {
    display: block;
    font-weight: bold;
    margin-bottom: 2px; /* Added margin-bottom */
}

input[type="text"],
input[type="password"],
input[type="number"] { /* Added input[type="number"] */
    width: 100%;
    padding: 10px; /* Increased padding */
    border: 1px solid #dd6fff;
    border-radius: 10px; /* Rounded corners */
    box-sizing: border-box;
    font-size: 12px;
    margin-top: 2px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5); /* Shadow effect */

}

input[type="text"]:hover,
input[type="password"]:hover,
input[type="number"]:hover { /* Added input[type="number"] */
    width: 100%;
    padding: 10px; /* Increased padding */
    border: 1px solid #dd6fff;
    border-radius: 10px; /* Rounded corners */
    box-sizing: border-box;
    font-size: 12px;
    margin-top: 2px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 1); /* Shadow effect */

}

select {
    width: 50%;
    padding: 12px;
    border: 1px solid #dd6fff;
    border-radius: 10px;
    box-sizing: border-box;
    font-size: 12px;
    margin-top: 5px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5); /* Shadow effect */

}

/* Placeholder for select */
select option[disabled] {
    color: #cccccc;
}

button {
    width: 100%;
    padding: 12px; /* Increased padding */
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: #ffffff;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s; /* Added transition */
}

@media screen and (max-width: 768px) {

    .adminlogin-wrapper{
        padding-top: 50px;
    }

    .adminlogin-container {
        width: 90vw; /* Set width to 50% for small screens */
        height: 75vh;
        padding-top: 50px;
    }

}